footer 
	.footer-wrapper-top
		background: #f5f5f5
		padding: 50px 0 10px
		.logo-text
			p
				position: relative
				font-size: 24px
				max-width: 200px
				font-weight: 500
				line-height: 30px
				text-transform: uppercase
				&:after
					content: ''
					display: block
					position: absolute
					right: 0
					top: 25%
					transform: translateY(-50%)
					width: 50px
					height: 1px
					background: #333333
		h2
			font-size: 16px
			font-weight: 600
			text-transform: uppercase
			margin-bottom: 20px
		p
			font-size: 13px
			caret-color: #333333
			font-weight: 400
			line-height: 18px
			margin-bottom: 20px

	.footer-wrapper-bottom
		padding: 3rem 0
		.container
			position: relative
			.nav
				padding-bottom: 5rem
				.title 
					font-size: 18px
					text-transform: uppercase
					color: #ffffff
					padding-bottom: 30px
				nav
					display: flex
					flex-direction: column
					max-width: 90%
					a 
						color: #999999
						font-size: 12px 
						text-shadow: 2px 2px 2px #000000
						line-height: 24px
						&:hover,&:active
							color: #ffffff
							transition: all .3s ease-in-out
				.project
					padding: 0
					a 
						font-size: 18px 
						color: #ffffff
						text-transform: uppercase
						line-height: 40px
					.button 
						display: flex
						.fb,.yt,.in
							width: 30px
							height: 30px
							border: 1px solid #ffffff
							display: flex
							justify-content: center
							align-items: center
							margin-right: 15px
							&:hover,&:active
								background: #8c2004
								transition: all 0.3s ease-in-out
								border: none
								em 
									color: #ffffff
			#gotop
				position: absolute
				top: 0rem 
				right: 0
				width: 50px
				height: 50px
				background: #f5f5f5 
				display: flex
				text-align: center
				align-items: center
				justify-content: center
				font-size: 35px
		.copy-right
			display: flex
			align-items: center
			.left
				p 
					font-size: 12px
					color: #999999 
					a 
						color: #999999
						&:hover 
							text-decoration: underline
							transition: all 0.3s ease-in-out
			.right 
				margin-left: auto
				ul 
					list-style: none 
					li
						display: inline-block
						margin-left: 5px
						a 
							font-size: 12px 
							color: #999999
						&:hover
							a 
								color: #ffffff
								transition: all .3s ease-in-out
					li:nth-child(2)
						position: relative
						&:after 
							position: absolute
							content: '|'
							display: block
							left: -6px
							top: 0
							color: #999999
					li:nth-child(3)
						position: relative
						&:after 
							content: '|'
							position: absolute
							display: block
							left: -6px
							top: 0
							color: #999999
		@media (max-width: 768px)
			.copy-right
				flex: 0 0 100%
				max-width: 100%
				flex-wrap: wrap
				.left
					flex: 0 0 100%
					max-width: 100%
					text-align: center
				.right
					flex: 0 0 100%
					max-width: 100%
					text-align: center
		

			   