.product-list1-nav
	padding-top: 3.75rem 
	.product-wrapper
		padding: 0 30px
		.title-h1-grey
			margin-left: 2rem
    		padding: 0 2rem
		.box-top
			margin-bottom: 2rem
			.content 
				p 
					font-size: 18px
					font-weight: 300
					color: #666666
			@media (max-width: 1025px)
				.content
					padding-bottom: 2rem
			nav
				margin-left: 2rem
				a 
					display: inline-block
					position: relative
					flex: 0 0 50%
					transition: all .3s ease-in-out
					margin-bottom: 24px
					padding-left: 30px
					max-width: 50%
					color: #999999
					font-size: 14px
					font-weight: 600
					text-transform: uppercase
					&:before 
						position: absolute
						width: 15px
						height: 15px
						background: #000000
						content: ''
						left: 0 
						top: 50%
						transform: translateY(-50%)
						display: block
						opacity: 0
						transition: all .4s ease-in-out
					&:hover,&.active
						position: relative
						color: #000000
						&:before
							opacity: 1
			@media (max-width: 1025px)
				nav 
					margin: 0
		@media (max-width: 1025px)
			.box-top
				padding: 0 15px